import React, { useEffect, useState } from "react";
import { Card, Carousel, Row, Col } from "antd";
import * as Realm from "realm-web";

import { Separator, VideoSlide } from "components/common";

const overVideoText = {
  color: "white",
  fontWeight: "400",
};

const contentStyle = {
  minHeight: "400px",
  display: "flex",
  alignItems: "flex-end",
  justifyContent: "left",
  padding: 50,
  background: "#364d79",
  backgroundSize: "cover",
  backgroundPosition: "center",
};

const CarouselNews = ({ news }) => {
  const backgroundCover = {
    background:
      'linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.8)), url("/img/covers/' +
      news.content.cover +
      '")',
  };
  return (
    <div>
      <div style={{ ...contentStyle, ...backgroundCover }}>
        <div style={{ width: "80%" }}>
          <h2 style={{ color: "rgb(240, 240, 240)" }}>{news.title}</h2>
          <p style={{ color: "rgb(240, 240, 240)" }}>{news.content.text}</p>
        </div>
      </div>
    </div>
  );
};

const Home = ({}) => {
  const [news, setNews] = useState([]);

  useEffect(() => {
    const app = new Realm.App({ id: "web-app-myllw" });
    const credentials = Realm.Credentials.apiKey(
      "9NCYWjxeuT26kYKHdB8zEzDEAr6z6nBw2BBplVdbWYnpibHip7zMgupLh2M68wuf"
    );

    app.logIn(credentials).then((user) => {
      user.functions.getAllNews().then((allNews) => {
        //console.log(news);
        setNews(allNews);
      });
    });
  }, []);

  return (
    <>
      <VideoSlide src="img/intro.mp4" first>
        <h2 style={overVideoText}>Welcome to</h2>
        <h1 style={overVideoText}>Weird Frames</h1>
      </VideoSlide>
      {news && (
        <div className="slide">
          <div className="container">
            <Row>
              <Col md={{ span: 24, offset: 0 }} lg={{ span: 12, offset: 6 }}>
                <h1>What's New</h1>
                <Separator height={2} />
                <Carousel autoplay>
                  {news &&
                    news.map((el, idx) => {
                      return <CarouselNews key={idx} news={el} />;
                    })}
                </Carousel>
              </Col>
            </Row>
          </div>
        </div>
      )}
    </>
  );
};

export default Home;
